        methods: {
            onDelete() {
                this.$confirm({
                    title: '请确认操作',
                    confirmText: "确定删除",
                    cancleText: "取消删除",
                    message: "永久删除该文件，是否继续？"
                }).then(() => {
                    this.$success({
                        message: '删除成功',
                        duration: 1500
                    });
                }).catch(() => {
                    this.$info({
                        message: '已取消删除',
                        duration: 1500
                    });
                })
            }
        }
</pre>
